export const accardion = () => {
	const accardion = $(".js-accardion-link");

	accardion.on("click", (e) => {
		const target = $(e.currentTarget);
		const parent = target.closest('.js-accardion');

		parent.toggleClass("is-active").siblings().removeClass("is-active");

		parent.children(".js-accardion-body").slideToggle();

		parent.siblings().children(".js-accardion-body").slideUp();
	});
};

export const accardionManyInit = function accardionManyInit() {
	var e = $(".js-accardion-many-link");
	e.on("click", (function (e) {
		var t = $(e.currentTarget).closest('.js-accardion-many');
		t.toggleClass("is-active"), t.children(".js-accardion-body").slideToggle();
	}))
}