import { toast } from '@/js/components/toast';

export const switcherToasts = () => {
	$('[data-toast-switcher]').on('change', (e) => {
		console.log(e.target);
		if(e.target.checked){
			toast.showToastSubscribeAdd('   ivanov@mail.ru,<br>      ');
		}else{
			toast.showToastSubscribeRemove(' <br>    ');
		}
	})
}