export const youReviewNameEdit = () => {
	$('.user-your-review__field .modal__edit').on('click', (e) => {
	console.log($(e.target).closest('.user-your-review__left'))
		const $parent = $(e.target).closest('.user-your-review__left');
		const focusedInput = $parent.find('.user-your-review__edit input').eq(0)[0];
		
		$parent.addClass('field-edit');

	  	focusedInput.focus();
 		focusedInput.selectionStart =  focusedInput.value.length;

 		if(focusedInput.value > 0){
 			$(this).find('input').eq(0).next('.modal__clear').addClass('active');
 		}
	});
};