/* eslint-disable no-new */
import Swiper from "swiper";
import { Navigation, Scrollbar, FreeMode, Pagination } from "swiper/modules";

const photoSlider = new Swiper(".photo-slider .swiper", {
    modules: [Navigation, Scrollbar, FreeMode],
    loop: false,
    slidesPerView: 'auto',
    freeMode: true,
    spaceBetween: 8,
    scrollbar: {
        el: '.navigation--scrollbar',
        draggable: false,
        hide: true,
    },
    on: {

        afterInit: function() {
            const swiperInstance = this;
            swiperInstance.scrollbar.updateSize(); //   scrollbar
            swiperInstance.scrollbar.el.classList.add('swiper-scrollbar-show'); //     scrollbar
        },
        sliderMove: function() {
            const swiperInstance = this;
            swiperInstance.scrollbar.el.classList.remove('swiper-scrollbar-show');
        },
        transitionEnd: (swiper)=> {
            setTimeout(()=>{
                let $scrollbar = $(swiper.scrollbar.el);
                $scrollbar.css({opacity: 0})
            }, 200)
        },
    }
});

