.switcher input[type=checkbox].switch {
  --active: var(--primary-color);
  --active-inner: #fff;
  --focus: 2px rgba(0, 194, 147, .3);
  --border: #fff;
  --border-hover: unset;
  --background: #ABB6CC;
  --disabled: #F6F8FF;
  --disabled-inner: #E1E6F9;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 27px;
  height: 16px;
  outline: none;
  display: inline-block;
  vertical-align: top;
  position: relative;
  margin: 0;
  cursor: pointer;
  background: #ABB6CC;
  background: var(--b, var(--background));
  transition: background 0.3s, border-color 0.3s, box-shadow 0.2s;
  border-radius: 11px;
}
.switcher input[type=checkbox].switch::after {
  content: "";
  box-sizing: border-box;
  display: block;
  position: absolute;
  transition: 0.3s, opacity 0.2s;
  transition: 0.3s, opacity var(--d-o, 0.2s);
  left: 0;
  top: 0;
  margin: 2px;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  background: var(--ab, var(--border));
  transform: translateX(0);
  transform: translateX(var(--x, 0));
}
.switcher input[type=checkbox].switch:checked {
  --b: var(--active);
  --bc: var(--active);
  --d-o: .3s;
  --d-t: .6s;
  --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
  --ab: var(--active-inner);
  --x: 11px;
}
.switcher input[type=checkbox].switch:disabled {
  --b: var(--disabled);
  cursor: not-allowed;
  opacity: 0.9;
}
.switcher input[type=checkbox].switch:disabled:checked {
  --b: var(--disabled-inner);
  --bc: var(--border);
}
.switcher input[type=checkbox].switch:disabled + label {
  cursor: not-allowed;
}
.switcher input[type=checkbox].switch:disabled:not(:checked):after {
  opacity: 0.6;
}
.switcher input[type=checkbox].switch:hover:not(:checked):not(:disabled) {
  --bc: var(--border-hover);
}
.switcher input[type=checkbox].switch:not(.switch) {
  width: 21px;
}
.switcher input[type=checkbox].switch:not(.switch):after {
  opacity: 0;
  opacity: var(--o, 0);
}
.switcher input[type=checkbox].switch:not(.switch):checked {
  --o: 1;
}
.switcher input[type=checkbox].switch + label {
  font-size: 14px;
  line-height: 21px;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  margin-left: 4px;
}

