import {closeSegmentedControl, showSegmentedControl} from "@/js/components/segmentedControl";

require.context('@/images/certeficates', false, /\.png$/)
require.context('@/images/blog', false, /\.png$/)
require('@/images/icons/user-circle.svg')

function checkIdle(){
    let hasClass = $('.fancybox__slide.is-selected').hasClass('is-draggable')

    //   
    if (hasClass) {
        $('.segmented-control').css({opacity: 0})
        $('.fancybox__footer').css({opacity: 0})
        $('.fancybox__toolbar').css({opacity: 0})
    }
    else {
        $('.segmented-control').css({opacity: 1})
        $('.fancybox__footer').css({opacity: 1})
        $('.fancybox__toolbar').css({opacity: 1})
    }
}

let fadeTimeout;
let fancyboxSlidesLength = 0;
let fancybox = Fancybox.bind("[data-fancybox]", {
    placeFocusBack: false,
    dragToClose: false,
    contentDblClick: false,
    touch: false,
    animated: false,
    Toolbar: {
        animated: false,
        display: {
            left: ["infobar"],
            right: ["close"],
        },
    },
    Images: {
        animated: false,
        initialSize: "fit",
        zoom: false,
        Panzoom: {
            animated: false,
            maxScale: 2,
            zoom: true,
            pinchToZoom: true,
            touch: true,
        },
    },
    Carousel: {
        animated: false,
        Navigation: false,
        infinite: false,
    },
    Thumbs: {
        animated: false,
        type: "classic",
        Carousel: {
            on: {
                ready: (fancybox) => {
                    fancyboxSlidesLength = fancybox.slides.length;
                    fadeTimeout = undefined;
                    addSliderContainer();
                },
            },
            Panzoom: {
                on: {
                    beforeTransform: (fancybox, thumb) => {
                        updateSlider();
                    }
                }
            }
        }
    },
    on: {
        "*": (carousel, eventName) => {
            // console.log(`Carousel eventName: ${eventName}`);
        },
        init: (fancybox) => {
            showSegmentedControl(fancybox);
        },
        done: ()=> {
            $('.f-button[data-fancybox-close]').on('click', ()=>{
                $('.fancybox__container').hide()
            })

            //     
            setTimeout(()=>{
                $('.fancybox__background-guard').each(function(){
                    $(this).remove();
                })
                $('.fancybox__content').addClass('ready');
            }, 100)
        },
        close: (fancybox) => {
            closeSegmentedControl(fancybox);
        },
        setIdle: (fancybox) => {
            fancybox.endIdle()
        }
    },
});
function addSliderContainer() {
    let $thumbsContainer = $('.f-thumbs__viewport');
    if ($thumbsContainer.length > 1)
        $thumbsContainer = $($thumbsContainer[$thumbsContainer.length-1]);

    let thumbWidth = 64; //    
    let gap = 10; //     
    let totalThumbsWidth = fancyboxSlidesLength * thumbWidth + (fancyboxSlidesLength - 1) * gap;
    let containerWidth = $thumbsContainer.width();

    if (totalThumbsWidth <= containerWidth) return; //   ,   

    if (!$thumbsContainer.find('.fancybox-slider-container').length) {
        let $sliderContainer = $('<div>', { class: 'fancybox-slider-container' });
        let $slider = $('<div>', { class: 'fancybox-slider', id: 'thumb-slider' });
        $thumbsContainer.append($sliderContainer);
        $sliderContainer.append($slider);

        setSliderWidth($slider);
    }
}

function setSliderWidth($slider) {
    let $track = $('.f-thumbs__track');
    if ($track.length > 1)
        $track = $($track[$track.length-1]);

    let thumbWidth = 64; //    
    let gap = 10; //     
    let totalThumbsWidth = fancyboxSlidesLength * thumbWidth + (fancyboxSlidesLength - 1) * gap;
    let trackWidth = (thumbWidth + gap) * fancyboxSlidesLength - gap; //   
    let containerWidth = $track.parent().width(); //  
    let sliderWidth = (containerWidth / trackWidth) * 100;

    if (totalThumbsWidth <= containerWidth) return;

    $slider.css({
        width: sliderWidth + '%',
    });
}

function updateSlider() {
    let $slider = $('#thumb-slider');
    let $track = $('.f-thumbs__track');
    if ($track.length > 1)
        $track = $($track[$track.length-1]);

    let thumbWidth = 64; //    
    let gap = 10; //     
    let totalThumbsWidth = fancyboxSlidesLength * thumbWidth + (fancyboxSlidesLength - 1) * gap;
    let trackWidth = (thumbWidth + gap) * fancyboxSlidesLength - gap; //   
    let containerWidth = $track.parent().width(); //  

    if (totalThumbsWidth <= containerWidth) return;

    let transformMatrix = $track.css('transform').replace(/[^0-9\-.,]/g, '').split(',');
    let translateX = parseInt(transformMatrix[4]);

    let sliderLeft = (-translateX / trackWidth) * 100;

    $slider.css({
        left: sliderLeft + '%'
    });

    //   ,   
    if (fadeTimeout) {
        clearTimeout(fadeTimeout);
    }

    //       
    $slider.stop(true, true).fadeIn(300);
    if (fadeTimeout) {
        //     
        fadeTimeout = setTimeout(() => {
            $slider.fadeOut(300);
        }, 200);
    } else
        fadeTimeout = 1;
}