export const headerScroll = () => {
	var header = document.querySelector('.header__middle'),
		headerTop = document.querySelector('.header__top'),
		headerHeight = header.offsetHeight + headerTop.offsetHeight,
		lastScrollTop = 0;

	window.addEventListener('scroll', function() {
		var scrollDistance = window.scrollY;

		if (scrollDistance > lastScrollTop && scrollDistance >= headerHeight) {
			//    ,        --  
			header.classList.add('header__middle--hidden');
		} else {
			header.classList.remove('header__middle--hidden');
			//           --  
		}

		if (scrollDistance === 0) {
			//    -  
			header.classList.remove('header__middle--hidden');
		}

		lastScrollTop = scrollDistance;
	});
};


