export const adversting = () => {
	const ad = $(".advertising__text, .attachment-banner__adv-text");
	const close= $('.advertising__close');

	ad.on("click", (e) => {
		const target = $(e.currentTarget);
		const targetId = target.data("id");
		let targetBlock = $(`.advertising__block[data-id="${targetId}"]`);

		targetBlock.toggleClass("is-active");
	});

	close.on("click", (e) => {
		const target = $(e.currentTarget);
		target.closest('.advertising__block.is-active').removeClass('is-active');
	});

	$(window).on("scroll", (e) => {
		const advs = $('.advertising__block.is-active');
		advs.removeClass('is-active');
	})

	$(document).mouseup((e) => {
		const container = $(".advertising__block");
		if (
			!container.is(e.target) &&
			!ad.is(e.target) &&
			container.has(e.target).length === 0
		) {
			container.removeClass("is-active");
		}
	});
};