import successImage from '@/images/statuses/success.svg';
import warningImage from '@/images/statuses/warning.svg';
import closeImage from '@/images/statuses/close.svg';
import regionImage from '@/images/statuses/region.svg';
import subsAddImage from '@/images/statuses/subs-add.svg';
import subsRemoveImage from '@/images/statuses/subs-remove.svg';

class ToastService {
    constructor() {
        this.toast = new bootstrap.Toast($(".toast"), {
            autohide: true,
            delay: 4000,
            animation: true,
        });
        this.currentOnClick = null; //     
    }
    setSettings(imageSrc, imageCSS, buttonCSS, buttonText, text = "") {
        $('.toast-image').attr('src', imageSrc).css(imageCSS);
        $('.toast-text').html(text);
        $('.toast-button').html(buttonText).css(buttonCSS);

        if (this.currentOnClick) {
            $('.toast-button').off('click', this.currentOnClick);
            this.currentOnClick = null;
        }

        this.toast.show();
    }
    showToastSuccess(text) {
        $('.toast-body').css({padding: 16});
        this.setSettings(
            successImage,
            {'background-color': 'unset', 'width': '24px', 'height': '24px', 'margin': '0 12px 0 0'},
            {'width': 'fit-content', 'padding': '0', 'margin-right': '-4px'},
            `<img src="${closeImage}" alt="close" width="34" height="34">`,
            text
        );
        this.currentOnClick = () => {this.toast.hide()};
        $('.toast-button').on('click', this.currentOnClick).removeAttr('href');
    }
    showToastWarning(text) {
        $('.toast-body').css({padding: 16});
        this.setSettings(
            warningImage,
            {'background-color': 'unset', 'width': '24px', 'height': '24px', 'margin': '0 12px 0 0'},
            {'width': 'fit-content', 'padding': '0', 'margin-right': '-4px'},
            `<img src="${closeImage}" alt="close" width="34" height="34">`,
            text
        );
        this.currentOnClick = () => { this.toast.hide() };
        $('.toast-button').on('click', this.currentOnClick).removeAttr('href');
    }
    showToastProduct(image, text, href) {
        $('.toast-body').css({padding: '6px 16px 6px 6px'});
        this.setSettings(
            image,
            {'background-color': '#fff', 'width': '66px', 'height': '66px', 'margin': '0 12px 0 0'},
            {'padding': '6px 16px', 'margin': ''},
            ``,
            text
        );
        this.currentOnClick = () => { window.location.href = href; };
        $('.toast-button').on('click', this.currentOnClick);
    }
    showToastRegion(text) {
        $('.toast-body').css({padding: 16});
        this.setSettings(
            regionImage,
            {'background-color': 'unset', 'width': '32px', 'height': '32px', 'margin': '0 12px 0 0'},
            {'width': 'fit-content', 'padding': '0', 'margin-right': '-4px'},
            `<img src="${closeImage}" alt="close" width="100%" height="100%">`,
            ` <br> ${text}`
        );
        this.currentOnClick = () => { this.toast.hide() };
        $('.toast-button').on('click', this.currentOnClick).removeAttr('href');
    }
    showToastSubscribeAdd(text) {
        $('.toast-body').css({padding: 16});
        this.setSettings(
            subsAddImage,
            {'background-color': 'unset', 'width': '32px', 'height': '32px', 'margin': '0 12px 0 0'},
            {'width': '34px', 'padding': '0', 'margin-right': '-4px'},
            `<img src="${closeImage}" alt="close" width="100%" height="100%">`,
            text
        );
        this.currentOnClick = () => { this.toast.hide() };
        $('.toast-button').on('click', this.currentOnClick).removeAttr('href');
    }
    showToastSubscribeRemove(text) {
        $('.toast-body').css({padding: 16});
        this.setSettings(
            subsRemoveImage,
            {'background-color': 'unset', 'width': '32px', 'height': '32px', 'margin': '0 12px 0 0'},
            {'width': '34px', 'padding': '0', 'margin-right': '-4px'},
            `<img src="${closeImage}" alt="close" width="100%" height="100%">`,
            text
        );
        this.currentOnClick = () => { this.toast.hide() };
        $('.toast-button').on('click', this.currentOnClick).removeAttr('href');
    }
}

export const toast = new ToastService();
