export const authModals = () => {
	const enterModal = new bootstrap.Modal($("#enterModal"), {});
	const enterPreloaderModal = new bootstrap.Modal($("#enterPreloaderModal"), {});
	const passwordModal = new bootstrap.Modal($("#passwordModal"), {});
	const passwordFullModal = new bootstrap.Modal($("#passwordFullModal"), {});
	const passwordErrorModal = new bootstrap.Modal($("#passwordErrorModal"), {});

	const regModalEmail = new bootstrap.Modal($("#regModalEmail"), {});
	const regModalPhone = new bootstrap.Modal($("#regModalPhone"), {});
	const regModalPhoneFull = new bootstrap.Modal($("#regModalPhoneFull"), {});
	const enterRegModalEmail = new bootstrap.Modal($("#enterRegModalEmail"), {});
	const enterRegModalPhone = new bootstrap.Modal($("#enterRegModalPhone"), {});
	const passRepairModalEmail = new bootstrap.Modal($("#passRepairModalEmail"), {});
	const passRepairModalPhone = new bootstrap.Modal($("#passRepairModalPhone"), {});
	const regErrorModal = new bootstrap.Modal($("#regErrorModal"), {});

	const repairDefaultModal = new bootstrap.Modal($("#repairDefaultModal"), {});
	const repairEmailModal = new bootstrap.Modal($("#repairEmailModal"), {});
	const repairSuccessModal = new bootstrap.Modal($("#repairSuccessModal"), {});

	const phoneEnterErrorModal = new bootstrap.Modal($("#phoneEnterErrorModal"), {});
	
	const passwordViewToggler = (() => {
		if(document.querySelector('.eye-ico') !== null){
			[].forEach.call(document.querySelectorAll('.eye-ico'), function(el){
				el.onclick = function(e) {
					var passInputs = this.closest('.enter-modal__group').parentNode.querySelectorAll('.enter-modal__input[data-type="password"]');

					this.classList.toggle('unblind');

					[].forEach.call(passInputs, function(item){
						item.getAttribute('type') === 'password' ?
						item.setAttribute('type','text') :
						item.setAttribute('type','password');

					});
				}
			});
		}

	})();

	//    
	$('.enter-modal__input').bind('focus change input cut copy paste', function(e) {
		console.log('onfocus!')
		if($(this).val().length > 0){
			$(this).next('.modal__clear').addClass('active');
		}else{
			$(this).next('.modal__clear').removeClass('active');

		}
	});
	// End    

	//        
	$('.enter-modal__input').next('.modal__clear').on('click', function(){
		$(this).prev().val('');
	});
	// End        

	//       . 
	$("#enterModal, #passwordModal, #regModalEmail, #regModalPhone, #enterRegModalEmail, #enterRegModalPhone").on('shown.bs.modal', function () {
	  var focusedInput =  $(this).find('input').eq(0)[0];
	  	focusedInput.focus();
 		focusedInput.selectionStart =  focusedInput.value.length;

 		if(focusedInput.value > 0){
 			$(this).find('input').eq(0).next('.modal__clear').addClass('active');
 		}
	});
	// End       . 
 	
 	//     
 	
 	$('#authModalsButtons button').on('click', function(e) {
 		new bootstrap.Modal($("#"+$(this).data('popup')), {}).show();
 	});
 	
 	// End     

 	// Cuts temporary code
 		//    1.1
	 	$('#enterModal').find('.enter-modal__button').on('click', function(){
	 		if($('#enterModal').find('.enter-modal__input').val() == ''){
		 		$('#enterModal').find('.enter-modal__input').addClass('enter-modal__input--error');
		 		$('#enterModal').find('.enter-modal__group').addClass('enter-modal__group--error');	
	 		}
	 	});
 		// End    1.1

 		//    1.2
 		$('#enterPreloaderModal').find('.enter-modal__button').on('click', function(){
	 		if($('#enterPreloaderModal').find('.enter-modal__input').val().length < 19){
		 		$('#enterPreloaderModal').find('.enter-modal__input').addClass('enter-modal__input--error');
		 		$('#enterPreloaderModal').find('.enter-modal__group').addClass('enter-modal__group--error');	
	 		}
	 	});

	 	$("#enterPreloaderModal").on('shown.bs.modal', function () {
		  $(this).find('.enter-modal__input').addClass('enter-modal__input--error');
		  $(this).find('.enter-modal__group').addClass('enter-modal__group--error');
		});

		if($('#pass-change-button').length > 0){
			$('#pass-change-button').on('click', function(e) {
				$(this).addClass('button--loading');
			})
		}
 		// End    1.2
 	// End Cuts temporary code

	//    product-card.html
		$('label[for="purchase-switcher-1"]').on('click', () => {
			 enterModal.show();
		});
	//END    product-card.html

	return{
		enterModal,
		enterPreloaderModal,
		passwordModal,
		passwordFullModal,
		passwordErrorModal,
		regModalEmail,
		regModalPhone,
		regModalPhoneFull,
		enterRegModalEmail,
		enterRegModalPhone,
		passRepairModalEmail,
		passRepairModalPhone,
		regErrorModal,
		repairDefaultModal,
		repairEmailModal,
		repairSuccessModal,
		passwordViewToggler
	}
}