export const bottomSheet = () =>{
	function myBottomSheet(id, openTrigger, basetHeight){

		var bottomSheet = document.querySelector(id);
		var showModalBtn = document.querySelector(openTrigger);
		var hideModalBtn = document.querySelector(id).querySelector("#bottom-sheet-hide");
		var sheetOverlay = bottomSheet.querySelector(".bottom-sheet__sheet-overlay");
		var sheetContent = bottomSheet.querySelector(".bottom-sheet__content");
		var dragIcon = bottomSheet.querySelector(".bottom-sheet__drag-icon");
		var isDragging = false, startY, startHeight;

		this.init = ()=>{
			this.events();
		}

		this.events = ()=>{
			dragIcon.addEventListener("mousedown", this.dragStart);
			document.addEventListener("mousemove", this.dragging);
			document.addEventListener("mouseup", this.dragStop);
			dragIcon.addEventListener("touchstart", this.dragStart);
			document.addEventListener("touchmove", this.dragging);
			document.addEventListener("touchend", this.dragStop);
			sheetOverlay.addEventListener("click", this.hideBottomSheet);
			hideModalBtn.addEventListener("click", this.hideBottomSheet);
			showModalBtn.addEventListener("click", this.showBottomSheet);
		}

		this.showBottomSheet = ()=>{
		    bottomSheet.classList.add("show");
		    document.body.style.overflowY = "hidden";
		    this.updateSheetHeight(basetHeight);
		}
		this.updateSheetHeight = (height)=>{
		    sheetContent.style.height = `${height}vh`;
		    bottomSheet.classList.toggle("fullscreen", height === 100);
		}
		this.hideBottomSheet = (e)=>{
			console.log('hideBottomSheet')
			// console.log(e.target)
		    bottomSheet.classList.remove("show");
		    setTimeout(() => {
		    	if(!bottomSheet.classList.contains('show')){
		    		document.body.style.overflowY = "auto";

		    	}
		    }, 100);
		}
		this.dragStart = (e)=>{
		    isDragging = true;
		    startY = e.pageY || e.touches?.[0].pageY;
		    startHeight = parseInt(sheetContent.style.height);
		    bottomSheet.classList.add("dragging");
		}
		this.dragging = (e)=>{
		    if(!isDragging) return;
		    const delta = startY - (e.pageY || e.touches?.[0].pageY);
		    const newHeight = startHeight + delta / window.innerHeight * 100;
		    this.updateSheetHeight(newHeight);
		}
		this.dragStop = (e)=>{
		    isDragging = false;
		    bottomSheet.classList.remove("dragging");
		    const sheetHeight = parseInt(sheetContent.style.height);
		    if(e.target.closest('.bottom-sheet__body') == null){
		    	sheetHeight < 25 ? this.hideBottomSheet() : sheetHeight > 75 ? this.updateSheetHeight(100) : this.updateSheetHeight(50);

		    }
		}
		this.init();
		return{
			init: this.init,
			hideBottomSheet: this.hideBottomSheet
		}
	 }

	if($('#variants-bottomsheet').length > 0){
		console.log("pizda!")
		const varsSheet = new myBottomSheet('#variants-bottomsheet', '#variantsBottomsheetOpen', 58.86);
	}
	// const avalSheet = new myBottomSheet('#availability-bottomsheet', '#aval-bottomsheet-open', 90);


	$('#bottom-sheet-variants [name="bottomsheet-radio"]').on('change', (e) => {
		var targetKeyText = $(e.target).closest('.bottom-sheet-variants__label').find('.bottom-sheet-variants__key').text(),
			targetValueText = $(e.target).closest('.bottom-sheet-variants__label').find('.bottom-sheet-variants__value').text();

		$('.variant-select__val').text(targetKeyText);
		$('.variant-select__price').text(targetValueText);

		if($(e.target).closest('.bottom-sheet-variants__label').find('.bottom-sheet-variants__value').eq(0)[0].hasAttribute('data-not-available')){
			$('.variant-select__price').addClass('variant-select__price--gray');
		}else{
			$('.variant-select__price').removeClass('variant-select__price--gray');

		}
		varsSheet.hideBottomSheet();
	});
}
