import { toast } from '@/js/components/toast';
const addCart = () => {
	$(".js-add-cart").on("click", (e) => {
		const target = $(e.currentTarget);
		const second = target.closest('.product-cart__buttons').find('.js-add-second')

		target.hide();
		second.hide();
		target.parent().find(".js-add-block").show();

		const toastImage = target
			.closest(".product-cart")
			.find("img")
			.data("src");

		const toastHref = target
			.closest(".product-cart")
			.find('.product-cart__link')
			.attr("href")

		toast.showToastProduct(`${toastImage}`, '   ', `${toastHref}`)
	});
};

const plusCart = () => {
	$(".js-add-plus").on("click", (e) => {
		const target = $(e.currentTarget);
		let $input = target.parent().find("input");
		$input.val(parseInt($input.val()) + 1);
		$input.change();

		return false;
	});
};

const minusCart = () => {
	$(".js-add-minus").on("click", (e) => {
		const target = $(e.currentTarget);
		const second = target.closest('.product-cart__buttons').find('.js-add-second')

		let $input = target.parent().find("input");
		let count = parseInt($input.val()) - 1;
		if (count < 1) {
			count = 1;
			target.closest(".card-adding-block").find(".js-add-cart").show();
			target.closest(".card-adding-block").find(".js-add-block").hide();
			second.show();
		} else {
			count;
		}
		$input.val(count);
		$input.change();

		return false;
	});
};

const expandable = () => {
	$('.product-cart__expandable').click(function (event) {
		event.preventDefault(); //    
		var $nameContainer = $(this).closest('.product-cart__name');
		// ,   
		if ($nameContainer.hasClass('expanded')) {
			$nameContainer.removeClass('expanded'); //  
			$nameContainer.css('max-height', '72px'); //    
		} else {
			$nameContainer.addClass('expanded'); //  
			$nameContainer.css('max-height', 'none'); //    
		}
	});
};

export const addCartInit = () => {
	addCart();
	plusCart();
	minusCart();
	expandable();
};
