function debounce(func, wait, immediate) {
    let timeout;
    return function executedFunction(...args) {
        const context = this;
        const later = function() {
            timeout = null;
            if (!immediate) func.apply(context, args);
        };

        const callNow = immediate && !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) func.apply(context, args);
    };
}

export function segmentedControlInit() {
    $('.segmented-control__1:not(.active)').click(debounce(() => { checkSegmentedControl('product'); }, 200, true));
    $('.segmented-control__2:not(.active)').click(debounce(() => { checkSegmentedControl('cert'); }, 200, true));
}

let needToHide = true;

function checkSegmentedControl(type) {
    if ((type === 'product' && !$('.segmented-control__1').hasClass('active')) ||
        (type === 'cert' && !$('.segmented-control__2').hasClass('active'))) {
        //     
        if (!$('.fancybox__background-guard').length) {
            $('body').append(`
            <div class="fancybox__background-guard is-horizontal">
                <div class="fancybox__background-guard-wrapper"></div>
            </div>
        `);
        }

        needToHide = false;

        Fancybox.close();

        setTimeout(()=>{
            switch (type) {
                case 'product':
                    $('.segmented-control__1').addClass('active');
                    $('.segmented-control__2').removeClass('active');
                    $('[data-fancybox="product"]')[0].click();
                    break;
                case 'cert':
                    $('.segmented-control__1').addClass('active');
                    $('.segmented-control__2').removeClass('active');
                    $('[data-fancybox="cert"]')[0].click();
                    break;
                default:
                    return;
            }
        }, 10)
    }
}

export function showSegmentedControl(fancybox) {
    let trigger = $(fancybox.options.triggerEl);
    let type = trigger.data('fancybox');
    let parent = $('.segmented-control');
    let segmentedControl = $('.js-segmented');

    if(!fancybox.options.triggerEl.hasAttribute('data-segment-control')){
        segmentedControl.show();
        if (type === "product") {
            parent.find('label[for="tab-1"]').addClass('active');
            parent.find('label[for="tab-2"]').removeClass('active');
        } else if (type === "cert") {
            parent.find('label[for="tab-2"]').addClass('active');
            parent.find('label[for="tab-1"]').removeClass('active');
        }
    }

}

export function closeSegmentedControl(fancybox) {
    let segmentedControl = $('.js-segmented');
    if (needToHide) {
        segmentedControl.hide();
    } else {
        needToHide = true;
    }
}
